import '../css/babyFood.less'
import React, { useState, useEffect } from 'react';
import { Typography,Table,Col, Row,Radio,Input } from 'antd';
const { Search } = Input;
const { Title, } = Typography;

function BabyFood(){
    const [isLoading,setLoading] = useState(false)
    const [dataList,setDataList] = useState([])
    const [filters,setFilter] = useState({type:'all',keyword:''})
    const columns = [

        {
            title:'',
            dataIndex:'',
            key:''
        },
        {
            title:'',
            dataIndex:'',
            key:'',
            sorter: (a, b) => compareAB(a,b,'')
        },
        {
            title:'',
            dataIndex:'',
            key:'',
            sorter: (a, b) => compareAB(a,b,'') ,
        },
        {
            title:'',
            dataIndex:'',
            key:''
        },
        {
            title:'',
            dataIndex:'',
            key:''
        },
    ]
    function compareAB(a,b,label){
        if(a[label] > b[label]) return 1;
        if(a[label] < b[label]) return -1;
        else return 0;
    }
    function filterTable(data){
        let filterData = [...data]
        const type = filters.type
        switch(type){
            case '':
                filterData = filterData.filter(v=>v[''] === '')
                break;
            case '':
                filterData = filterData.filter(v=>v[''].includes(''))
                break;
            case '':
                filterData = filterData.filter(v=>v[''].includes(''))
                break;
            default:

        }
        return filterData.filter(v=> 
            v[''].includes(filters.keyword)||v[''].includes(filters.keyword))
    }
    const handleData = (data)=>{
        // antdkey
        const newData = data.map((value,index)=>{
            const category = value[''].slice(0,-1) //,
            value[''] = category
            const newObj = {...value,key:index}
            return newObj
        })
        return newData
    }
    function changeFilter(type,v){
        const newFilter = {...filters}
        newFilter[type] = v
        setFilter(newFilter)
    }
    const onChangeType = (e)=>{
        const activeValue = e.target.value
        changeFilter('type',activeValue)
    }
    const onSearch = (v)=>{
        changeFilter('keyword',v)
    }
    useEffect(()=>{
        const fetchUrl =async()=>{
            try{
                setLoading(true)
                const response = await fetch(`${process.env.PUBLIC_URL}/baby_food.json`)
                const data = await response.json();
                const newDataList = handleData(data)
                setDataList(newDataList)
                setLoading(false)
            }catch(error){
                console.error(error)
                setLoading(false)
            }
            
        }
        fetchUrl()
    },[])
    
    return (
        <>
         <Title level={3}>(0-12) </Title>
         
        <Row className="baby-food-item">
            <Col span={16}>
                <Radio.Group onChange={(e)=>onChangeType(e)} value={filters.type} buttonStyle="solid">
                    <Radio.Button value="all"></Radio.Button>
                    <Radio.Button value="">(0-12)</Radio.Button>
                    <Radio.Button value="">(6-12)</Radio.Button>
                    <Radio.Button value=""></Radio.Button>
                </Radio.Group>
            </Col>
            <Col span={8}>
                <Search
                    placeholder="..."
                    allowClear
                    enterButton="Search"
                    onSearch={onSearch}
                    />
            </Col>
        </Row>
        
         <Table
            loading={isLoading}
            columns={columns}
            expandable={{
            expandedRowRender: (list) => (
                <>
                    <Row style={{'marginLeft':'50px'}}>
                        <Col span={3}></Col>
                        <Col span={20}>{list['']}</Col>
                        <Col span={3}></Col>
                        <Col span={20}>{list['']}</Col>
                        <Col span={3}></Col>
                        <Col span={11}>{list['']}</Col>
                        <Col span={3}></Col>
                        <Col span={5}>{list['']}</Col>
                    </Row>
                    <Row style={{'margin':'15px 50px'}} >
                        <Col span={3}></Col>
                        <Col span={2}>{list['']}</Col>
                        <Col span={2}></Col>
                        <Col span={2}>{list['']}</Col>
                        <Col span={2}></Col>
                        <Col span={3}>{list['']}</Col>
                        <Col span={2}></Col>
                        <Col span={8}>{list['']}</Col>
                    </Row>
                </>
                ),
            }}
            dataSource={filterTable(dataList)}
            />
        </>
       
    )
}

export default BabyFood