import React, { useState, useEffect,useMemo } from 'react';
import { Typography,Space,BackTop,Input } from 'antd';
import InforCard from '../components/Card';
import ErrorHint from '../components/Error';
import Assort from '../components/Select';

const { Search } = Input;
const { Title, } = Typography;

function Feeding(){
    const [isLoading,setLoading] = useState(false);
    const [isError,setError] = useState(false)
    const [roomData,setRoomData] = useState([])
    const [areaOpts,setAreaOpts] = useState([])  
    const [areaValue,setAreaValue] = useState('all')
    const [keywords,setKeyword] = useState('')

    //select value()
    function handleOpt(value){
        setAreaValue(value)
        setLoading(true)
        window.scrollTo({top: 0, behavior: 'smooth'});
        setTimeout(()=>{
            setLoading(false);
        },700)
    }
    //
    function filterContent(data){
        const areaData = areaValue === 'all'? data:data.filter((v)=> v[''] === areaValue);
        if(keywords.length>0) {
            return areaData.filter((v)=> v[''].indexOf(keywords) > -1 ||
            v[''].indexOf(keywords) > -1)
        }
        return areaData
    }
    function onSearch(v){
        setKeyword(v)
    }
    useEffect(()=>{
        const loadData = ()=> {
            setLoading(true)
            fetch(`${process.env.PUBLIC_URL}/feeding.json`)
            .then((res)=> res.json())
            .then((data)=>{
                setRoomData(data)
                setLoading(false)
            })
            .catch((error)=>{
                console.warn(error)
                setLoading(false);
                setError(true)
            })
        }
        loadData()
    },[])
 
    useMemo(()=>{
        if(roomData.length>0){
            let result = new Set();
            //Set
            roomData.forEach((v)=> result.add(v['']));
            const options = Array.from(result,(v)=> Object.assign({},{label:v,value:v}))
            setAreaOpts([{label:'',value:'all'},...options])
        }
        
    },[roomData])

    return(
        <>
        <Title level={3}></Title>
        <Space align="center" size="large" style={{margin:'40px 0'}}>
            
            <Assort onChange={(v)=>handleOpt(v)} 
            value={areaValue} opts={areaOpts}/>
            
            <Search
                placeholder=".."
                allowClear
                enterButton="Search"
                size="large"
                onSearch={onSearch}
                />
        </Space>
        {
            isError ? <ErrorHint /> : 
            <InforCard loading={isLoading} data={filterContent(roomData)}
                title={(i)=>i['']}
                area={(i)=>i['']}
                clickTag={(i)=>handleOpt(i[''])}
                mapLink={(i)=> handleMapLink(i)}
                search={(i)=>searchData(i)}
                content={(i)=>handleData(i)}>
            </InforCard>
        }
        <BackTop />
        </>
    )
}

//
function handleData(item){
    const time = `${item['']}`
    const tel = `${item['']}`
    const address = `${item['']}`
    return (<>
                <p>{time}</p>
                <p>{tel}</p>
                <p>{address}</p>
            </>)
}
//-
function searchData(keyword){
    if(keyword !== null){
        const _url = `https://www.google.com.tw/search?q=${keyword['']}`
        window.open(_url)
    }
    return false
}
//-
function handleMapLink(infor){
    if(infor !== null){
        const _url = `https://www.google.com.tw/maps/place/${infor['']}`
        window.open(_url)
    }
    return false
}

export default Feeding